  .root {
    height: 100vh;
    background-image: url("../bg.webp");
    background-size: 100% auto; 
    background-repeat: no-repeat;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .box {
    font-size: 16px;
    font-weight: 700;
    text-align: center;
  }
  .box1{
    border: solid;
    border-color: #999999;
    border-radius: 10px;
    padding: 20px 24px 24px;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    gap: 4px;
  }
  
  .box-title{
    font-size: 15px;
    font-weight:600;
    display: flex;
    gap: 12px;
    align-items: center;
  }
  
  .box-title img {
    width: 32px;
    height: 32px;
  }
  
  .mini{
    font-size: 12px;
    color: #999999;
    font-weight: 400;
  }
  
  .container {
    width: 370px;
    display: flex;
    flex-direction: column;
    gap: 24px;
  }
  
  .blok{
    color: #999999;
    font-size: 12px;
    display: flex;
    gap: 12px;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    margin: 0 auto;
    width: 100vh;
    cursor: pointer;
    text-align: center
  }
  
  a:visited {
    color: #999999
  }
  a {
    text-decoration: none;
  }
  a:hover {
    text-decoration: underline;
  }
  
  .night-theme-btn{
    position: fixed;
    left: 15px;
    top: 15px;
    border: none;
    outline: none;
    width: 60px;
    height: 60px;
    border-radius: 8px;
    background-color: rgba(71, 60, 71, 0.5);
    transition: filter .4s;
    cursor: pointer;
  }
  
  .night-theme-btn:hover{
    filter: brightness(50%);
  }
  
  .night-theme-btn:active{
    filter: brightness(80%);
  }
  
  .root.night-theme{
    background-color: rgb(29, 29, 29);
    color: aliceblue;
  }
  
  .root.night-theme .night-theme-btn{
    background-color: rgba(244, 240, 244, 0.5);
  }